import React, {useEffect, useState} from "react";
import { Grid } from "@mui/material";
import Button from "@mui/material/Button";
import Input from "@mui/material/Input";
import IconButton from "@mui/material/IconButton";
import PhotoCamera from "@mui/icons-material/PhotoCamera";
import { ToastContainer, toast } from 'react-toastify';
import axios from 'axios';
import 'react-toastify/dist/ReactToastify.css';
import "./gallery.css";
import CircularProgress from '@mui/material/CircularProgress';


const Gallery = () => {

    const [imageFiles, setImageFiles] = useState([])
    const [imageList, setImageList] = React.useState([]);
    const [isLoading, setIsLoading] = useState(false);
    const [addDisable, setAddDisable] = useState(false);
    const orgId = localStorage.getItem('orgId');

    useEffect(()=>{
        getImageList();
    },[])

    const getImageList = () => {
        setIsLoading(true);
        axios({
          method: "get",
          url: `http://13.235.70.211:9090/v1/org/${orgId}`,
          config: { headers: { "Content-Type": "application/json" } },
        })
        .then((res) => {
            console.log(res);
            if (res?.data) {
                setImageList( res.data);
                setTimeout(() => {
                  setIsLoading(false);
                }, 2000);
            }
        })
        .catch((err) => {
            console.log(err.response);
            setIsLoading(false);
        });
    };


    const handleImagesUpload = (e) => {
        console.log(e.target.files, "imageupload");
        if(e.target.files) {
            setImageFiles(e.target.files)
            toast.success('Image(s) are added!')
        } else if(!e.target.files){
            toast.error('Images are required!');
        }
    }


    const handleSubmitImages = () => {
        setAddDisable(true);
        let file = imageFiles.length > 0 ? imageFiles : [];
        let formData = new FormData();
        for (let i = 0; i < file.length; i++) {
            formData.append("files", file[i]);
        }

        for (let pair of formData.entries()) {
            console.log(pair[0] + ': ' + pair[1]);
        }

        axios({
            method: "post",
            url: `http://13.235.70.211:9090/v1/org/${orgId}`,
            data: formData,
            headers: {  "Accept": "application/json" ,"Content-Type": "multipart/form-data" },
        })
        .then((res)=>{
            console.log(res,'res');
            setAddDisable(false);
            getImageList();
            if (res?.data?.responseCode === 200) {
                toast.success('Image(s) has been uploaded successfully!');
            }
        })
        .catch((err)=>{
          console.log(err);
          setAddDisable(false);
        })
    }


    return (
        <div className="image-page">
            <ToastContainer
                position="top-center"
                autoClose={3000}
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover
                toastStyle={{color:'#000000', fontWeight:'lighter', fontFamily:'Open Sans, sans-serif', fontSize:'12px'}}
            />
          <div className="image-title">Gallery Management</div>
          <div className="image-sub-title">Manage gallery for organisation events.</div>
    
          <Grid container spacing={2}  >
            <Grid container item xs={12} sm={12} md={5}>
              <div className="image-card-head">
                <Grid container item xs={12}>
                  <div>Upload organisation images</div>
                  <div className="baseline"></div>
                </Grid>
    
                <div className="support-text">
                  * Supported files .jpg, .jpeg, .png{" "}
                </div>
    
                <Grid container item xs={12} sm={2} sx={{ marginTop: "1rem" }}>
                  <label htmlFor="contained-button-file">
                    <Input
                      
                      id="contained-button-file"
                      multiple
                      type="file"
                      sx={{ display: "none" }}
                      onChange={(e) => handleImagesUpload(e)}
                      accept="image/*"
                    />
                    <Button
                      variant="outlined"
                      style={{
                        width: "300px",
                        height: "45px",
                        color: "#ED4D6E",
                        border: "1px solid #ED4D6E",
                      }}
                      component="span"
                    >
                      Upload
                      <IconButton
                        style={{ color: "#ED4D6E" }}
                        aria-label="upload picture"
                        component="span"
                      >
                        <PhotoCamera />
                      </IconButton>
                    </Button>
                  </label>
                </Grid>
    
                <button disabled={addDisable} onClick={handleSubmitImages} className={addDisable ? "audio-add-disable-btn" : "audio-add-btn"}>Add Images to Gallery</button>
              </div>
            </Grid>
            <Grid container item xs={12} sm={12}  md={7}>
            
              <div className="image-cardlist-head">
                <Grid container item xs={12}>
              
                  <div className="image-list-title">Gallery Images List</div>
                  
                  <div className="baseline"></div>
                </Grid>
                <Grid container item xs={12}>
                  {
                    isLoading ? 
                    <div className="imagelist-loader">
                      <CircularProgress />
                    </div>
                    :
                    <div className="image-table-div">
                    <table>
                    <thead>
                      <tr>
                        <td>S. No</td>
                        <td>Image Url</td>
                      </tr>
                    </thead>
    
                    <tbody>
                        {imageList.length > 0 ? (
                            (() => {
                            let counter = 1;
                            console.log("Images : ", imageList)
                            return imageList.flatMap((el) =>
                                el.images.map((url) => (
                                <tr key={counter}>
                                    <td>{counter++}</td>
                                    <td>{url.substring(url.lastIndexOf('/') + 1)}</td>
                                </tr>
                                ))
                            );
                            })()
                        ) : (
                            <tr>
                            <td colSpan="2" className="imagelist-no-data">No Image Data Available</td>
                            </tr>
                        )}
                    </tbody>
                  </table>
                  </div>
                  }
               
                </Grid>
                {/* <div className="audio-sub-title">No Audio Files</div> */}
              </div>
            </Grid>
          </Grid>
        </div>
      );
}

export default Gallery;