import React from 'react';
import Logo from "./../../assets/wisher-logo-sm.png";
import GridViewIcon from '@mui/icons-material/GridView';
import Diversity3Icon from '@mui/icons-material/Diversity3';
import BookmarkBorderIcon from '@mui/icons-material/BookmarkBorder';
import MailIcon from '@mui/icons-material/Mail';
import AccessTimeIcon from '@mui/icons-material/AccessTime';
import CollectionsIcon from '@mui/icons-material/Collections';
import MessageIcon from '@mui/icons-material/Message';
import SettingsIcon from '@mui/icons-material/Settings';
import { Link,Routes, Route, NavLink } from "react-router-dom";
import MenuIcon from '@mui/icons-material/Menu';

import Dashboard from '../Dashboard';
import AudioList from '../AudioList';
import MembersList from '../MembersList';

import './header.css';
import AddMembers from '../AddMembers';
import Gallery from '../Gallery';
import PrivateRoute from '../Authorization';
import AxiosInterceptor from "../Utils/AxiosUtils";
import AddOrganisationEvents from '../OrganisationEvents';

const menus = [
    'Dashboard',
    'Add Members',
    'Members List',
    'Audio Management',
    'Gallery',
    'Organisation Events',
    'Help',
    'Settings'
]

const Header = () => {
    AxiosInterceptor();
    const [openDrawer, setOpenDrawer] = React.useState(false);
    const [activeClass, setActiveClass] = React.useState('');
    const handleActive = (id) => {
        setActiveClass(id);    
    }

    const handleActiveMobile= (id) => {
        setActiveClass(id);
        setOpenDrawer(false);    
    }

  return (
    <>  
    <div className="main-header">
        <div className='main-header-logo-icon'>
             <img className='header-logo' src={Logo} alt="logo" /> 
            <div className="hamburger" onClick={()=>setOpenDrawer(!openDrawer)} ><MenuIcon style={{color:'#ED4D6E', height:'46px', width:'56px'}} /> </div>
        </div>
        <div className='header-right'>
            <div className='profile-pic'>
                {/* <img className='profile-pic' src="https://i.pinimg.com/originals/f8/f5/98/f8f598fb70de7de910b93b10ef7fb9e5.jpg" alt="pro" /> */}
            </div>
            <div className='header-profile-details'>
                <div className='profile-name'>{localStorage.getItem('organisationName')}</div>
                <div className='profile-role'>{localStorage.getItem('emailId')}</div>
            </div>
        </div>
        
    </div>

    {/* //sidebar */}
<div className='second-section' >
    {/* { openDrawer ?  */}
    <div className={openDrawer ? 'sidebar-main desktop-none' : 'sidemain-toggle-close '}>
        <div className='drawer-head'>
            <div>
            <img className='drawer-logo' src={Logo} alt="logo" />
             </div>
            <div>
            <div className="hamburger" onClick={()=>setOpenDrawer(!openDrawer)} ><MenuIcon style={{color:'#ED4D6E', height:'46px', width:'56px'}} /> </div>
            </div>
        </div>
        <div className='menu-div' >
        {
            menus.map((el,i) => {
               return ( 
                   <NavLink key={i}  
                   className='menu-head'  
                   onClick={()=>handleActiveMobile(i)}
                   
                   to={el === "Dashboard" ? '/home/dashboard' : 
                   el === 'Members List' ? '/home/members' :
                   el === 'Audio Management' ? '/home/audios' :
                   el === 'Add Members' ? '/home/addmembers' :
                   el === 'Organisation Events' ? '/home/event':
                   el === 'Gallery' ? '/home/gallery' : ' '} >
                <div > 
                 {
                     el === 'Dashboard' ? <GridViewIcon className={i == activeClass ? 'menu-icon-active' : 'menu-icon'} /> :
                     el === 'Add Members' ? <BookmarkBorderIcon className={i == activeClass ? 'menu-icon-active' : 'menu-icon'}/> :
                     el === 'Members List' ? <AccessTimeIcon className={i == activeClass ? 'menu-icon-active' : 'menu-icon'}/> :
                     el === 'Audio Management' ? <MailIcon className={i == activeClass ? 'menu-icon-active' : 'menu-icon'} /> :
                     el === 'Gallery' ? <CollectionsIcon className={i == activeClass ? 'menu-icon-active' : 'menu-icon'} /> :
                     el === 'Organisation Events' ? <Diversity3Icon className={i == activeClass ? 'menu-icon-active' : 'menu-icon'} /> :
                     el === 'Help' ? <MessageIcon className={i == activeClass ? 'menu-icon-active' : 'menu-icon'} /> :
                     el === 'Settings' ? <SettingsIcon className={i == activeClass ? 'menu-icon-active' : 'menu-icon'} /> : <BookmarkBorderIcon className='menu-icon' />
                 }
                
                </div>       
               <div className={i == activeClass ? 'menu-act' : 'menu'}>{el}</div>
               </NavLink>
               );
            })
        }
        
        </div>

        
 </div>
 {/* : ''
} */}

<div className='permanent-sidebar-main'>
        <div className='drawer-head'>
            <div>
            <img className='drawer-logo' src={Logo} alt="logo" />
             </div>
            {/* <div>
            <div className="hamburger" onClick={()=>setOpenDrawer(!openDrawer)} ><MenuIcon style={{color:'#ED4D6E', height:'46px', width:'56px'}} /> </div>
            </div> */}
        </div>
        <div className='menu-div'>
        {
            menus.map((el,i) => {
               return (
                   <NavLink key={i}
                    className='menu-head' 
                   
                   onClick={()=>handleActive(i)}
                   
                   to={el === "Dashboard" ? '/home/dashboard' : 
                   el === 'Members List' ? '/home/members' :
                   el === 'Audio Management' ? '/home/audios' :
                   el === 'Add Members' ? '/home/addmembers' : 
                   el === 'Organisation Events' ? '/home/event':
                   el === 'Gallery' ? '/home/gallery' : ' '} >
                <div > 
                 {
                     el === 'Dashboard' ? <GridViewIcon className= {i == activeClass ? 'menu-icon-active' : 'menu-icon'} /> :
                     el === 'Add Members' ? <BookmarkBorderIcon className={i == activeClass ? 'menu-icon-active' : 'menu-icon'}/> :
                     el === 'Members List' ? <AccessTimeIcon className={i == activeClass ? 'menu-icon-active' : 'menu-icon'}/> :
                     el === 'Audio Management' ? <MailIcon className={i == activeClass ? 'menu-icon-active' : 'menu-icon'} /> :
                     el === 'Gallery' ? <CollectionsIcon className={i == activeClass ? 'menu-icon-active' : 'menu-icon'} /> :
                     el === 'Organisation Events' ? <Diversity3Icon className={i == activeClass ? 'menu-icon-active' : 'menu-icon'} /> :
                     el === 'Help' ? <MessageIcon className={i == activeClass ? 'menu-icon-active' : 'menu-icon'} /> :
                     el === 'Settings' ? <SettingsIcon className={i == activeClass ? 'menu-icon-active' : 'menu-icon'} /> : <BookmarkBorderIcon className='menu-icon' />
                 }
                
                </div>       
               <div className={i == activeClass ? 'menu-act' : 'menu'} >{el}</div>
               </NavLink >
               );
            })
        }
        
        </div>
 </div>

    <div className='main-page'>
        <Routes>
            <Route element={<PrivateRoute />}>
                <Route path="/dashboard" element={<Dashboard />} />
                <Route path="audios" element={<AudioList /> } />
                <Route path="members" element={<MembersList /> } /> 
                <Route path="addmembers" element={<AddMembers /> } /> 
                <Route path="gallery" element={<Gallery /> } /> 
                <Route path="event" element={<AddOrganisationEvents />} />
            </Route>
        </Routes>
    </div>
    </div>


    </>
  )
}

export default Header;