import React, { useEffect, useState } from "react";

import Grid from '@mui/material/Grid';

import './dashboard.css';
import { ToastContainer, toast } from "react-toastify";
import { CircularProgress } from "@mui/material";
import CalendarMonthIcon from '@mui/icons-material/CalendarMonth';
import GroupIcon from '@mui/icons-material/Group';
import { useNavigate } from "react-router-dom";
import axios from "axios";

const Dashboard = () => {

  const navigate = useNavigate();

  const [isLoading, setIsLoading] = useState(false);
  const [users, setUsers] = useState(0);
  const [orgEvents, setOrgEvents] = useState(0);

  useEffect(()=>{
    getAnalytics();
  },[])

  const getAnalytics = () => {
    setIsLoading(true);
    axios({
      method: "get",
      url: `http://localhost:9090/v1/organisation/7/analytics`,
      config: { headers: { "Content-Type": "application/json" } },
    })
      .then((res) => {
        console.log(res);
        if (res) {
          if (res.data) {
            setUsers(res.data.users);
            setOrgEvents(res.data.orgEvents);
            setTimeout(() => {
              setIsLoading(false);
            }, 2000);
          }
        }
      })
      .catch((err) => {
        console.log(err.response);
        setIsLoading(false);
      });
  };

  return (
    <div className="dashboard-div">
      <ToastContainer
            position="top-center"
            autoClose={3000}
            hideProgressBar={false}
            newestOnTop={false}
            closeOnClick
            rtl={false}
            pauseOnFocusLoss
            draggable
            pauseOnHover
            toastStyle={{color:'#000000', fontWeight:'lighter', fontFamily:'Open Sans, sans-serif', fontSize:'12px'}}
        />

        <Grid container spacing={2} >
          <Grid item xs={12} sm={12} md={5}>
            <div className="dashboard-title">
              Dashboard
            </div>

            <div className="dashboard-sub-title">
              Manage all the data and analytics
            </div>
          </Grid>
        </Grid>

        <Grid container spacing={2}  >
        <Grid container item xs={6} sm={6} md={3}>
          <div className="audio-card-head">
            <Grid container item xs={12} style={{borderBottom: "1px solid black"}}>
              <div className="analytics-title"><GroupIcon/><h4>Members</h4></div>
            </Grid>
            <div className="analytics">
              <div className="border-radius">
                <h2>{users}</h2>
              </div>
            </div>
            <div className="btn-holder">
              <button onClick={() => navigate("/home/members")} className="analytics-btn">View Users</button>
            </div>
          </div>
        </Grid>

        <Grid container item xs={6} sm={6} md={3}>
          <div className="audio-card-head">
            <Grid container item xs={12} style={{borderBottom: "1px solid black"}}>
              <div className="analytics-title"><CalendarMonthIcon/> <h4>Organisation Events</h4></div>
            </Grid>
            <div className="analytics">
              <div className="border-radius">
                <h2>{orgEvents}</h2>
              </div>
            </div>
            <div className="btn-holder">
              <button onClick={() => navigate("/home/event")} className="analytics-btn">View Events</button>
            </div>
          </div>
        </Grid>

        <Grid container item xs={12} sm={12}  md={6}>
          <div className="audio-cardlist-head">
            <Grid container item xs={12}>
          
              <div className="audio-list-title">Audio List</div>
              
              {/* <div className="audio-search-div"> */}
              {/* <TextField
                id="outlined-select-currency"
                select
                label="Search By Type"
                value={type}
                size="small"
                onChange={handleSearchType}
                style={{width:'300px', height:"30px"}}
                variant="outlined"
                inputProps={{ style: { fontSize: '14px', fontFamily:'Open Sans, sans-serif' } }}
                InputLabelProps={{ style: { fontSize: '12px' } }}
              >
              {eventNameArray.map((option) => (
                <MenuItem key={option.value} value={option.value}>
                  {option.label}
                </MenuItem>
              ))}
            </TextField> */}
                
                {/* <button
                  // onClick={() => handleSearchAudio()}
                  className={"audio-search-btn"}
                >
                  Search
                </button>
                </div> */}
              
         
              <div className="baseline"></div>
            </Grid>
            <Grid container item xs={12}>
              {
                isLoading ? 
                <div className="audiolist-loader">
                  <CircularProgress />
                </div>
                :
                <div className="audio-table-div">
                <table>
                <thead>
                  <tr>
                    <td>Audio</td>
                    <td>Type</td>
                    <td>Audio Url</td>
                    
                  </tr>
                </thead>

                {/* <tbody>
                  {audioList.length > 0 ? (
                    audioList.map((el, i) => {
                      return (
                        <tr key={el.audioId}>
                          <td>{el.audioDescription}</td>
                          <td>{el.audioType}</td>
                          <td>{el.audioUrl.split('/').pop()}</td>
                         
                          
                        </tr>
                      );
                    })
                  ) : (
                    <div className="audiolist-no-data"> No Data Available </div>
                  )}
                </tbody> */}
              </table>
              </div>
              }
           
            </Grid>
            {/* <div className="audio-sub-title">No Audio Files</div> */}
          </div>
        </Grid>
      </Grid>


    </div>
  );
};

export default Dashboard;
