import React, { useState } from "react";
import Logo from "./../../assets/wisher-logo-sm.png";
import { TextField } from "@mui/material";
import { useNavigate } from "react-router-dom";
import "./register.css";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import axios from "axios";

const Register = () => {
  const navigate = useNavigate();

  const [signinData, setSigninData] = useState({organisationName: "",address:"", emailId:"", phoneNumber:"", password:""})
  const [isLoading, setIsLoading] = useState(false);
  const [disabledBtn, setDisabledBtn] = useState(false);

  const handleChange = (e) => {
    setSigninData(prev => ({...prev, [e.target.name] : e.target.value}));
    console.log(signinData.password)
  }

  const handleSubmitSignin = () => {
    if(signinData) {
      axios.post('http://13.235.70.211:9090/signup', signinData, {
        headers: {
          'Content-Type': 'application/json'
        },
        // withCredentials: true, 
      })
      .then((response) => {
        if(response){
          if(response.data){
            setSigninData({})
            console.log(`Org Name : ${signinData.organisationName}, password : ${signinData.password}`)
            toast.success(`Organisation ${signinData.organisationName} registered successfully`);
            navigate("/login");
            setIsLoading(false);
            setDisabledBtn(false);
          }
        }
      })
      .catch((err) => {
        if (err) {
          if (err) {
            if (err.data) {
              toast.error(err.data.description);
            } else if(err.response.data.responseCode === 403) {
              toast.error("Organisation already exist with this email ID")
            }else {
              toast.error('An error occurred, please try again.');
            }
          } else {
            toast.error('No response from the server.');
          }
        } else {
          toast.error('An error occurred, please try again.');
        }
        console.error('There was an error!', err);
      })
      .finally(() => {
        setIsLoading(false);
        setDisabledBtn(false);
      });
    } else{
      setTimeout(()=>{
        setIsLoading(false);
        setDisabledBtn(false);
      },1000)
      toast.error('All details are required!')
    }
  }

  return (
    <div className="register-body">
      <ToastContainer
            position="top-center"
            autoClose={3000}
            hideProgressBar={false}
            newestOnTop={false}
            closeOnClick
            rtl={false}
            pauseOnFocusLoss
            draggable
            pauseOnHover
            toastStyle={{color:'#000000', fontWeight:'lighter', fontFamily:'Open Sans, sans-serif', fontSize:'12px'}}
        />
      <div className="register-form-card">
        <div className="form-title">
          <img src={Logo} height="50" alt="prop" />{" "}
        </div>

        <div className="form-div">
          <div className="input-field">
            <TextField fullWidth id="outlined-required" label="Organisation Name" name="organisationName" value={signinData.organisationName}  onChange={(e) => handleChange(e)} />
          </div>
          <div className="input-field">
            <TextField fullWidth id="outlined-required" label="Address" name="address" value={signinData.address}  onChange={(e) => handleChange(e)} />
          </div>
          <div className="input-field">
            <TextField fullWidth id="outlined-required" label="Email Address" name="emailId" value={signinData.emailId} onChange={(e) => handleChange(e)} />
          </div>
          <div className="input-field">
            <TextField fullWidth id="outlined-required" label="Phone Number" name="phoneNumber" value={signinData.phoneNumber}  onChange={(e) => handleChange(e)} />
          </div>
          <div className="input-field">
            <TextField
              fullWidth
              id="outlined-password-input"
              label="Password"
              type="password"
              name="password"
              value={signinData.password}
              onChange={(e) => handleChange(e)}
            />
          </div>

          <div className="register-btn-div">
            <button
              onClick={handleSubmitSignin}
              className="register-btn"
            >
              Register
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};
export default Register;