import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import Grid from "@mui/material/Grid";
import CircularProgress from "@mui/material/CircularProgress";
import DeleteForeverIcon from "@mui/icons-material/DeleteForever";
import EditIcon from "@mui/icons-material/Edit";
import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "./memberList.css";
import { TextField } from "@mui/material";
import GlobalModal from "../Modal";
import CloseIcon from '@mui/icons-material/Close';

const MembersList = () => {
  const [memberList, setMemberList] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [searchEmail, setSearchEmail] = useState("");
  const [modalOpen,setModalOpen] = useState(false);
  const [deleteData,setDeleteData] = useState(null);
  const orgId = localStorage.getItem('orgId');
  const navigate = useNavigate();

  useEffect(() => {
    getMemberList();
  }, []);

  const getMemberList = () => {
    setIsLoading(true);
    axios({
      method: "get",
      url: `http://13.235.70.211:9090/v1/user/${orgId}`,
      config: { headers: { "Content-Type": "application/json" } },
    })
      .then((res) => {
        console.log(res,'ooooo');
        if (res) {
          if (res.data) {
            setMemberList(res.data);

            setTimeout(() => {
              setIsLoading(false);
            }, 2000);
          }
        }
      })
      .catch((err) => {
        if(err){
          if(err.response){
            if(err.response.data){
              if(err.response.data.responseCode === 403){
                toast.error(err.response.data.description);
                setMemberList([]);
                setTimeout(() => {
                  setIsLoading(false);
                }, 1000);
              }else{
                toast.error(err.response.data.responseCode);
                setTimeout(() => {
                  setIsLoading(false);
                }, 1000);
              }

            }
          }
        }
      });
  };

  const handleEdit = (el) => {
    navigate('/home/addmembers', {
      state:{
        updateObj: el
      }
    })
  }

  const handleClose = ()=>{
    setModalOpen(false);
  }



  const handleToastMessage = ()=>{
    getMemberList();
    toast.success("User has been deleted successfully");
  }


  const handleDelete = (el) => {
    setDeleteData(el);
    setModalOpen(true);

  };
  const handleReset = () => {
    setSearchEmail('');
    getMemberList();
  }
  const handleSearch = () => {
    setIsLoading(true);
    axios({
      method: "get",
      url: `http://13.235.70.211:9090/v1/user/${orgId}/${searchEmail}`,
      config: { headers: { "Content-Type": "application/json" } },
    })
      .then((res) => {
        console.log(res);
        if (res) {
          if (res.data) {
            setMemberList([res.data]);

            setTimeout(() => {
              setIsLoading(false);
            }, 1000);
          }
        }
      })
      .catch((err) => {
        console.log(err.response);
        setIsLoading(false);
      });
  };

  return (
    <div className="member-list-div">
      <ToastContainer
        position="top-center"
        autoClose={3000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
        toastStyle={{
          color: "#000000",
          fontWeight: "lighter",
          fontFamily: "Open Sans, sans-serif",
          fontSize: "12px",
        }}
      />
      <GlobalModal modalOpen={modalOpen}  getMemberList={getMemberList} handleToastMessage={handleToastMessage} deleteData={deleteData} handleClose={handleClose} />
      <Grid container spacing={2}>
        <Grid item xs={12} sm={12}>
          <div className="member-title">Member List</div>

          <div className="member-sub-title">
            Manage all the data and analytics
          </div>

          {isLoading ? (
            <div className="member-loader">
              <CircularProgress />
            </div>
          ) : (
            <div id="member-list">
              <div className="search-div">
                <TextField
                  id="outlined-basic"
                  size="small"
                  label="Search By Email"
                  variant="outlined"
                  // inputProps={{ style: { fontSize: 13 } }}
                  InputLabelProps={{ style: { fontSize: 12 , zIndex:0} }}
                  onChange={(e) => setSearchEmail(e.target.value)}
                  value={searchEmail}
                  className="search"
                  InputProps={{
                    endAdornment: (
                      <CloseIcon  onClick={() => handleReset()} style={{color:'#535251', cursor:'pointer'}} />
                    )
                  }}
                />
                <button
                  onClick={() => handleSearch()}
                  className={"member-search-btn"}
                >
                  Search
                </button>
                {/* <button
                  onClick={() => handleReset()}
                  className={"member-reset-btn"}
                >
                  Reset
                </button> */}
              </div>
              <div className="member-table-div">
              <table>
                <thead>
                  <tr>
                    <td>Name</td>
                    <td>Email</td>
                    <td>Whatsapp Number</td>
                    <td>Video Received To</td>
                    <td>Action</td>
                  </tr>
                </thead>

                <tbody>
                  {memberList.length > 0 ? (
                    memberList.map((el, i) => {
                      return (
                        <tr key={el.userId}>
                          <td>{el.userFullName}</td>
                          <td>{el.emailId}</td>
                          <td>{el.whatsAppNumber}</td>
                          <td>
                            {el.messageMedium === "BOTH"
                              ? "Whatsapp/Email"
                              : el.messageMedium}
                          </td>
                          <td>
                            <div className="action-box">
                              <span>
                                <DeleteForeverIcon
                                  onClick={() => handleDelete(el)}
                                  style={{
                                    color: "#ED4D6E",
                                    cursor: "pointer",
                                  }}
                                />{" "}
                              </span>
                              <span>
                                <EditIcon
                                onClick={() => handleEdit(el)}
                                  style={{
                                    color: "#ED4D6E",
                                    cursor: "pointer",
                                  }}
                                />{" "}
                              </span>
                            </div>
                          </td>
                        </tr>
                      );
                    })
                  ) : (
                    <div className="no-data"> No Data Available </div>
                  )}
                </tbody>
              </table>
              </div>
            </div>
          )}
        </Grid>
      </Grid>
    </div>
  );
};

export default MembersList;
