import axios from 'axios';
import { isTokenValid } from './JwtUtils';
import { useNavigate } from 'react-router-dom';

const AxiosInterceptor = () => {

    const navigate = useNavigate();

    const excludedEndpoints = ['/login', '/signup'];

    axios.interceptors.request.use(
    (config) => {
        const token = localStorage.getItem('token');
        const orgId = localStorage.getItem('orgId');
        const isExcludedEndpoint = excludedEndpoints.some(endpoint => config.url.includes(endpoint));
        if (!isExcludedEndpoint) {
            if (token && isTokenValid(token, orgId)) {
                config.headers['Authorization'] = `Bearer ${token}`;
            } else {
                navigate('/login');
            }
        }
        return config;
    },
    (error) => {
        return Promise.reject(error);
    }
    );

    axios.interceptors.response.use(
    (response) => response,
    (error) => {
        if (error.response && error.response.status === 401) {
        localStorage.removeItem('token');
        localStorage.removeItem('orgId');
        localStorage.removeItem('organisationName');
        localStorage.removeItem('emailId')
        navigate('/login');
        }
        return Promise.reject(error);
    }
    );

}

export default AxiosInterceptor;
