import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import TextField from "@mui/material/TextField";
import MenuItem from "@mui/material/MenuItem";
import Button from "@mui/material/Button";
import Grid from "@mui/material/Grid";
import Input from "@mui/material/Input";
import IconButton from "@mui/material/IconButton";
import PhotoCamera from "@mui/icons-material/PhotoCamera";
import moment from "moment";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import axios from "axios";
import RemoveRedEyeIcon from '@mui/icons-material/RemoveRedEye';
import DeleteIcon from '@mui/icons-material/Delete';
import CircularProgress from '@mui/material/CircularProgress';
import GlobalModal from "../Modal";
import "./addMembers.css";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
} from "@mui/material";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';


let eventNameArray = [
  {
    value: "Birthday",
    label: "Birthday",
    files: [],
  },
  {
    value: "Wedding Day",
    label: "Wedding Day",
    files: [],
  },
  {
    value: "Joining Day",
    label: "Joining Day",
    files: [],
  },
];

const videoReceivedTypeArray = [
  {
    value: "BOTH",
    label: "Both",
  },
  {
    value: "EMAIL",
    label: "Email",
  },
  {
    value: "WHATSAPP",
    label: "Whatsapp",
  },
];
const AddMembers = () => {
  const [eventName, setEventName] = React.useState("Birthday");
  const [videoReceivedType, setVideoReceivedType] = React.useState("BOTH");
  const [imageFiles, setImageFiles] = React.useState(null);
  const [birthday, setBirthday] = React.useState(null);
  const [weddingDay, setWeddingDay] = React.useState(null);
  const [joiningDay, setJoiningDay] = React.useState(null);
  const [birthday2, setBirthday2] = React.useState(null);
  const [weddingDay2, setWeddingDay2] = React.useState(null);
  const [joiningDay2, setJoiningDay2] = React.useState(null);
  const [userFullName, setUserFullName] = React.useState("");
  const [email, setEmail] = React.useState("");
  const [whatsappNumber, setWhatsappNumber] = React.useState("");
  const [userId, setUserId] = useState("")
  const [addEventObj, setAddEventObj] = React.useState([]);
  const [isLoading, setIsLoading] = React.useState(false);
  const [disabledBtn, setDisabledBtn] = React.useState(false);
  const [modalOpen,setModalOpen] = React.useState(false);
  const [viewData, setViewData] = React.useState(null);

  const [viewType, setViewType] = React.useState(null)
  const [backup, setBackup] = React.useState(null)
  const location = useLocation();
  const [updateObj, setUpdateObj] = React.useState(location.state !== null ? location.state.updateObj : null)
  const orgId = localStorage.getItem('orgId');
  console.log(location,updateObj,'jjjjjjj');

  useEffect(()=>{
    const data = {
      eventName: '',
      files:{
        event:[]
      },
    };
    const data2 = {
      eventName: '',
      files: {
        event:[]
      },
    };
    const data3 = {
      eventName: '',
      files: {
        event:[]
      },
    };
    console.log(updateObj,'topup')
    if(updateObj !== null){
      console.log(updateObj,'1up')
      setUserFullName(updateObj.userFullName);
      if(updateObj.userId) {
        setUserId(updateObj.userId)
      }
      setEmail(updateObj.emailId);
      setWhatsappNumber(updateObj.whatsAppNumber);
      setVideoReceivedType(updateObj.messageMedium);
      if(updateObj.imageUrls !== null || updateObj.imageUrls.length !== 0){

        updateObj.imageUrls.map((el)=>{
          console.log(el,'ellll')
          if(el.eventType === "BIRTH_DATE"){
            data.eventName= 'Birthday'
            data.files.event.push(el.imageUrl);
            console.log(data,'dataup')
            setAddEventObj([...addEventObj, data])
            // setAddEventObj([...addEventObj, {eventName: 'Birthday', files:[...this.files,el]}])
          }else   if(el.eventType === "WEDDING_DATE"){
            data2.eventName= 'Wedding Day'
            data2.files.event.push(el.imageUrl);
            setAddEventObj([...addEventObj, data2])
          }else   if(el.eventType === "JOINING_DATE"){
            data3.eventName= 'Joining Day'
            data3.files.event.push(el.imageUrl);
            setAddEventObj([...addEventObj, data3])
          }

        })
        console.log(addEventObj,'Fup')
        setAddEventObj([data,data2,data3])
      }


    }
  },[updateObj])

  const handleView = (el,type) => {
    setViewData(el);
    setViewType(type);
    setModalOpen(true);
  };
  const handleClose = ()=>{
    setModalOpen(false);
  }

  const handleChangeEventName = (event) => {
    setEventName(event.target.value);
  };

  const handleChangeType = (event) => {
    setVideoReceivedType(event.target.value);
  };

  const handleDay = (e) => {
    console.log(e);
    const ev = moment(e).format("YYYY-MM-DD");
    console.log(ev, eventName);

    if (eventName === "Birthday") {
      setBirthday(e);
      setBirthday2(ev);
    } else if (eventName === "Wedding Day") {
      setWeddingDay(e);
      setWeddingDay2(ev);
    } else if (eventName === "Joining Day") {
      setJoiningDay(e);
      setJoiningDay2(ev);
    }
  };

  const handleImageOnClick = (e)=>{
    e.target.value = null;
    e.target.files = null;
  }


  const handleImagesUpload = (e) => {

    const files = e.target.files;
    if(eventName === 'Birthday' ){
      eventNameArray.forEach((el, i) => {
        if (eventName === el.value) {
          el.files =  files;
        }
      });
      toast.success('Image(s) are added!');
      setImageFiles({ event: files });
    }else  if(eventName === 'Wedding Day'){
      eventNameArray.forEach((el, i) => {
        if (eventName === el.value) {
          el.files = files;
        }
      });
      toast.success('Image(s) are added!');
      setImageFiles({ event: files });
    } else  if(eventName === 'Joining Day' ){
      eventNameArray.forEach((el, i) => {
        if (eventName === el.value) {
          el.files = files;
        }
      });
      toast.success('Image(s) are added!');
      setImageFiles({ event: files });


    }
    
    // else if(!eventName){
    //    toast.error('Event Name is required!');
    // }
    // else if(birthday == null || joiningDay == null || weddingDay == null){
    //    toast.error('Event Date is required!');
    // }


  };
 console.log(imageFiles, eventNameArray,' hello')

  const deleteEvent = (event) => {
    console.log(event,addEventObj,imageFiles, eventNameArray,'xxxxxx')
     const newArray = addEventObj.filter((item) => {
       return item.eventName !== event.eventName
      })
      setAddEventObj(newArray);
   
       eventNameArray.map((el)=>{
        if(el.value === event.eventName){
          el.files = []
        }
      })
  }

  const handleAddEvent = () => {
    // console.log(eventNameArray, addEventObj, imageFiles,'zzzz6666')
  
    const data = {
      eventName: eventName,
      files: imageFiles,
    };
    if (imageFiles && eventName && (birthday || joiningDay || weddingDay)) {
      setAddEventObj([...addEventObj, data]);
      setImageFiles(null);
      setEventName('Birthday');
      setJoiningDay(null);
      setWeddingDay(null);
      setBirthday(null);
  
    } else{
      if(imageFiles == null && eventName == '' && birthday == null && joiningDay == null && weddingDay == null ){
        toast.error('Event Name, Event Date and Images are required!');
      }else if(imageFiles && eventName == '' && birthday == null && joiningDay == null && weddingDay == null){
        toast.error('Event Name and Event Date are required!');
      }else if(imageFiles == null && eventName && birthday == null && joiningDay == null && weddingDay == null){
        toast.error('Event Date and Images are required!');
      }else if(imageFiles !== null && eventName && birthday == null && joiningDay == null && weddingDay == null){
        toast.error('Event Date is required!');
      } else{
        toast.error('Images are required!');
      }
    }
    
  };

  const handleSubmit = () => {
    setIsLoading(true);
    setDisabledBtn(true);
    console.log(eventName, userFullName, email, videoReceivedType, joiningDay2 , birthday2, weddingDay2, joiningDay2)
    let birthDayImages = eventNameArray[0].files.length > 0 && eventNameArray[0].files;
    let weddingDayImages = eventNameArray[1].files.length > 0 && eventNameArray[1].files;
    let joiningDayImages = eventNameArray[2].files.length > 0 && eventNameArray[2].files;


    const data = {
      birthDay: birthday2,
      emailId: email,
      joiningDay: joiningDay2,
      messageMedium: videoReceivedType,
      userFullName: userFullName,
      weddingDay: weddingDay2,
      whatsAppNumber: whatsappNumber,
      orgId: orgId,
      userId: userId
    };

    
    let formData = new FormData();


    const json = JSON.stringify(data);
    const blob = new Blob([json], {
      type: 'application/json'
    });
    formData.append('data', blob);

    if(birthDayImages){
      for(let i=0;i<birthDayImages.length;i++){
        formData.append(
          "birthDayImages",
          birthDayImages[i]
        );
      }
    }
    if(weddingDayImages){
      for(let i=0;i<weddingDayImages.length;i++){
        formData.append(
          "weddingDayImages",
          weddingDayImages[i]
        );
      }
    }
    if(joiningDayImages){
      for(let i=0;i<joiningDayImages.length;i++){
        formData.append(
          "joiningDayImages",
          joiningDayImages[i]
        );
      }
    }
   
    if(userFullName && email && whatsappNumber && videoReceivedType ){
      const requestMethod = updateObj ? "put" : "post";
      const requestUrl = `http://13.235.70.211:9090/v1/user`;
      axios({
        method: requestMethod,
        url: requestUrl,
        data: formData,
        // config: { headers: { "Content-Type": "application/json" } },
        headers: {  "Accept": "application/json", "Content-Type": "multipart/form-data" },
      })
        .then((response) => {
          if(response){
            if(response.data){
              setEmail("");
              setUserFullName("");
              setWhatsappNumber("");
              setVideoReceivedType("BOTH")
              toast.success(`User has been ${updateObj ? 'updated' : 'created'} successfully`);
              setAddEventObj([]);
              setIsLoading(false);
              setDisabledBtn(false);
            }
          }
        })
        .catch((err) => {
          console.log(err, "err");
          setIsLoading(false);
          setDisabledBtn(false);
          if(err.response.data.description === "User already exist with given Email Id") {
            toast.error("User already exist with given Email Id")
          }
          if(err){
            if(err.response){
              if(err.response.data){
                toast.error(err.response.data.error);
              }
            }
          }
        });
    }else{
      setTimeout(()=>{
        setIsLoading(false);
        setDisabledBtn(false);
      },1000)
    
      toast.error('All details are required!')
    }    
  };

console.log(addEventObj,'up')
  return (
    <div className="addmembers-page">
      
          <ToastContainer
            position="top-center"
            autoClose={3000}
            hideProgressBar={false}
            newestOnTop={false}
            closeOnClick
            rtl={false}
            pauseOnFocusLoss
            draggable
            pauseOnHover
            toastStyle={{color:'#000000', fontWeight:'lighter', fontFamily:'Open Sans, sans-serif', fontSize:'12px'}}
        />
    <GlobalModal modalOpen={modalOpen}  viewData={viewData} viewType={viewType}  handleviewClose={handleClose} />

  {
    isLoading ?
    <div className="addmember-loader">
    <CircularProgress />
   </div>
     : ''

  }
  
      <div className="add-title"> { updateObj !== null ? 'Update Member' : 'Add Members'}</div>
      <div className="add-sub-title">
        Add details of new project on Special day wisher, and make their day
        special with us.
      </div>
      <div className="personal-card">
        <Grid container spacing={2}>
          <Grid container item xs={12}>
            <div className="member-personal-detail-title">Personal Details</div>
          </Grid>
          <Grid container item xs={12} sm={4} sx={{ marginTop: "1rem" }}>
            <TextField
              fullWidth
              id="outlined-basic"
              size="small"
              label="Name"
              variant="outlined"
              inputProps={{ style: { fontSize: 13 } }}
              InputLabelProps={{ style: { fontSize: 12 } }}
              onChange={(e) => setUserFullName(e.target.value)}
              value={userFullName}
              
            />
          </Grid>
          <Grid container item xs={12} sm={4} sx={{ marginTop: "1rem" }}>
            <TextField
              fullWidth
              id="outlined-basic"
              size="small"
              label="Email"
              variant="outlined"
              inputProps={{ style: { fontSize: 13 } }}
              InputLabelProps={{ style: { fontSize: 12, zIndex:0 } }}
              onChange={(e) => setEmail(e.target.value)}
              value={email}
            />
          </Grid>
          <Grid container item xs={12} sm={4} sx={{ marginTop: "1rem" }}>
            <TextField
              fullWidth
              type="number"
              id="outlined-basic"
              size="small"
              label="Whatsapp Mobile No"
              variant="outlined"
              inputProps={{ style: { fontSize: 13 }, maxLength: 10 }}
              InputLabelProps={{ style: { fontSize: 12, zIndex:0 } }}
              onInput={(e) => {
                e.target.value = Math.max(0, parseInt(e.target.value))
                  .toString()
                  .slice(0, 10);
              }}
              onChange={(e) => setWhatsappNumber(e.target.value)}
              value={whatsappNumber}
              

            />
          </Grid>
          <Grid container item xs={12} sm={6} sx={{ marginTop: "1rem" }}>
            <TextField
              id="outlined-select-currency"
              select
              size="small"
              fullWidth
              label="Video Received To"
              value={videoReceivedType}
              onChange={handleChangeType}
              variant="outlined"
              inputProps={{ style: { fontSize: 13 } }}
              InputLabelProps={{ style: { fontSize: 12 , zIndex:0} }}
            >
              {videoReceivedTypeArray.map((option) => (
                <MenuItem key={option.value} value={option.value}>
                  {option.label}
                </MenuItem>
              ))}
            </TextField>
          </Grid>
        </Grid>

        <Grid container spacing={2} sx={{ marginTop: "1rem" }}>
          <Grid container item xs={12}>
            <div className="member-detail-title">
              Event details and Photo Upload
              <div className="member-detail-desc">
                Below images are used for creating video's and minimum 3 to
                maximum 10 images are allowed to upload.
              </div>
            </div>
          </Grid>
          <Grid container item xs={12} sm={12} sx={{ marginTop: "1rem" }}>
            <Grid container item xs={12} sm={6}>
              <TextField
                id="outlined-select-currency"
                select
                
                label="Event Name"
                value={eventName}
                size="small"
                onChange={handleChangeEventName}
                className="event-select"
                // sx={{ marginTop: "0.5rem", width:'94%' }}
                variant="outlined"
                inputProps={{ style: { fontSize: 13 } }}
                InputLabelProps={{ style: { fontSize: 12 } }}
              >
                {eventNameArray.map((option) => (
                  <MenuItem key={option.value} value={option.value}>
                    {option.label}
                  </MenuItem>
                ))}
              </TextField>
              <DatePicker
                selected={
                    birthday
                    ? birthday
                    : weddingDay
                    ? weddingDay
                    : joiningDay
                    ? joiningDay
                    : null
                }
                onChange={(e) => handleDay(e)}
                className="date-picker"

                placeholderText="DD-MM-YYYY"
                InputLabelProps={{}}
              />
              {/* <TextField
                name="someDate"
                label="Event Date"
                size="small"
                InputLabelProps={{
                  shrink: true,
                  required: true,
                  style: { fontSize: 12 },
                }}
                
                type="date"
                fullWidth
                className="date-input"
                // defaultValue={values.someDate}
                variant="outlined"
                sx={{ marginTop: "2rem" }}
                value={birthday ? birthday : (weddingDay ? weddingDay : ( joiningDay ? joiningDay : null))}
                inputProps={{ style: { fontSize: 13 } }}
                onChange={(e)=>handleDay(e)}
              /> */}
              <div className="mobile-image-upload">
                <label htmlFor="mob-contained-button-file">
                  <Input
                    accept="image/*"
                    id="mob-contained-button-file"
                    multiple
                    fullWidth
                    type="file"
                    sx={{ display: "none", width:'90%' }}
                    onChange={(e) => handleImagesUpload(e)}
                    // onClick={(e)=> handleImageOnClick(e)}
                    inputProps={{
                      multiple: true,
                    }}
                  />
                  <Button variant="outlined" 

                  style={{width:'100%'}} component="span">
                    Upload
                    <IconButton
                      color="primary"
                      aria-label="upload picture"
                      component="span"
                    >
                      <PhotoCamera />
                    </IconButton>
                    <div className="upload-text">
                      *.png, .jpg, .jpeg are allowed
                    </div>
                  </Button>
                </label>
              </div>
              <div className="file-alert"> * Same file(s) should be chosen for next event </div>

              <div className="add-event-btn-div">
                <button onClick={handleAddEvent} className="add-event-btn">
                  Add Event
                </button>
              </div>
            </Grid>
            {/* <Grid
              container
              item
              xs={12}
              sm={2}
              sx={{
                marginTop: "1rem",
                marginLeft: "1rem",
                marginRight: "0.5rem",
              }}
            >
              <div className="image-upload">
                <label htmlFor="contained-button-file">
                  <Input
                    accept="image/*"
                    id="contained-button-file"
                    type="file"
                    sx={{ display: "none" }}
                    onChange={(e) => handleImagesUpload(e)}
                    inputProps={{
                      multiple: true,
                    }}
                  />
                  <Button variant="outlined" component="span">
                    Upload
                    <IconButton
                      color="primary"
                      aria-label="upload picture"
                      component="span"
                    >
                      <PhotoCamera />
                    </IconButton>
                    <div className="upload-text">
                      *.png, .jpg, .jpeg are allowed
                    </div>
                  </Button>
                </label>
              </div>
            </Grid> */}

            <Grid container item xs={12} sm={6}>
              {/* //   List */}
              <div className="event-list">
                <Table>
                  <TableHead>
                    <TableRow>
                      <TableCell width={120}>Event Name </TableCell>
                      <TableCell width={100}>Total Images </TableCell>
                      <TableCell width={140}>Actions</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {addEventObj.length > 0 ?
                      addEventObj.map((el, i) => {
                        return (
                          <TableRow key={i}>
                            <TableCell>{el.eventName}</TableCell>
                            <TableCell>
                              {el.files &&
                                el.files.event &&
                                el.files.event.length}
                            </TableCell>
                            <TableCell>
                              <span>
                            
                                <RemoveRedEyeIcon
                                  onClick={()=>handleView(el, 'members')}
                                  style={{
                                    color: "#ED4D6E",
                                    cursor: "pointer",
                                  }}
                                />{" "}
                              </span>
                            <span>
                                <DeleteIcon
                                  onClick={()=>deleteEvent(el)} 
                                  style={{
                                    color: "#ED4D6E",
                                    cursor: "pointer",
                                    marginLeft:'15px',
                                  }}
                                />{" "}
                              </span>
                        
                            </TableCell>
                          </TableRow>
                        );
                      })
                      : ''
                    }
                  </TableBody>
                </Table>
              </div>
            </Grid>
          </Grid>
          <Grid container item xs={12} sm={12}></Grid>
          <Grid container item xs={12} sm={12} sx={{ marginTop: "0.5px" }}>
            <button onClick={()=>handleSubmit()} disabled={disabledBtn ? true : false} className={disabledBtn ? "add-member-register-disabled-btn" : "add-member-register-btn"}>
              Register
            </button>
          </Grid>
        </Grid>
      </div>
    </div>
  );
};

export default AddMembers;
