import React, { useEffect } from 'react';
import Modal from 'react-modal';
import axios from 'axios';
import CloseIcon from '@mui/icons-material/Close';
import './modal.css';



// Make sure to bind modal to your appElement (https://reactcommunity.org/react-modal/accessibility/)
// Modal.setAppElement('#delete');

const GlobalModal = (props) => {
 console.log(props,'pops')

 const [data, setData] = React.useState([]);
 const orgId = localStorage.getItem('orgId');

 useEffect(()=>{
    let arr;
    if(props.viewData){
        if(props.viewData.files){
             arr =  Object.entries(props.viewData.files.event).map((e) => ( { [e[0]]: e[1] } ));
            setData(arr)
        }
    }

 },[props])

 console.log(data,'bjbjbjbj')

 let eventDelete=null;


 const handleDelete = () => {

    const el = props && props.deleteData;
    console.log(props.deleteData,'delete')

    eventDelete = props.deleteData.orgEventId;

    const url = !eventDelete
      ? `http://13.235.70.211:9090/v1/user/${el.emailId}`
      : `http://13.235.70.211:9090/v1/org/event/${orgId}/${encodeURIComponent(el.eventName)}`;

    axios({
        method: "delete",
        url: url,
        config: { headers: { "Content-Type": "application/json" } },
      })
        .then((res) => {
          console.log(res);
          if (res) {
            if (res.data) {
              if (res.data.responseCode === 200) {
                props.handleToastMessage();
                closeModal();
              }
            }
          }
        })
        .catch((err) => {
          console.log(err.response);
          closeModal();
        });

  }

//   function afterOpenModal() {
//     // references are now sync'd and can be accessed.
//     subtitle.style.color = '#f00';
//   }

  const closeModal = ()=> {
    console.log(props.viewType, props,'cfcfc')
    if(props.viewType === "members") props.handleviewClose();
     else props.handleClose();
  }
// console.log(props.viewData.files.event[0],'lllll')
  return (
    <div>
      <Modal
      appElement={document.getElementById('root')}
        isOpen={props.modalOpen}
        // onAfterOpen={afterOpenModal}
        onRequestClose={closeModal}
        // style={customStyles}
        className={props.viewType === "members" ? 'view-modal' : "modal"}
        contentLabel="Example Modal"
      >
        {
            props.viewType === "members" ?
            <div>
            <div className='view-event-div'>
                <div className='view-event-title' > Gallery View </div>
                <div> <CloseIcon onClick={closeModal} style={{marginRight:'1rem', cursor:'pointer' }} /> </div>
                </div>
            <hr/>
                <div className='view-event-body-head'>
                    <div className='view-event-body-title'> Event -  {props.viewData ? props.viewData.eventName : ''} </div>
                    <div className='gallery'> 
                    {/* <img  src={URL.createObjectURL(props.viewData.files.event[0])} alt="gal" /> */}
                        {
                            data ?
                            data.map((el,i)=>{
                                return (<div> 
                                    <div className='file-name'> {el[i].name} </div>
                                    <img className="image-grid"  src={URL.createObjectURL(el[i])} alt="event" />
                                </div>)
                            }
                            )
                   
                            : <div> No Data </div>
                        }
                    </div>
                </div>
           
            </div>
            :
            <div>
                 <div className='delete-event-div'>
                     <div className='delete-event-title' > Confirmation </div>
                    <div> <CloseIcon onClick={closeModal} style={{marginRight:'1rem', cursor:'pointer' }} /> </div>
                </div>
                <hr/>
                <div className='delete-title'>
                  Are you sure you want to delete the {!eventDelete ? 'user' : 'event'}?
                </div>
                <hr/>
            
                <div className='delete-btn-div'>
                 <button onClick={handleDelete} id="delete" className="modal-delete-btn"> DELETE </button>
                 <button onClick={closeModal} className="modal-cancel-btn"> CANCEL </button>
                </div>   
            </div>
        }
     

      </Modal>
    </div>
  );
}

export default GlobalModal;