import React, { useEffect, useState } from "react";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
import Grid from "@mui/material/Grid";
import Input from "@mui/material/Input";
import IconButton from "@mui/material/IconButton";
import PhotoCamera from "@mui/icons-material/PhotoCamera";
import moment from "moment";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import axios from "axios";
import RemoveRedEyeIcon from '@mui/icons-material/RemoveRedEye';
import DeleteIcon from '@mui/icons-material/Delete';
import CircularProgress from '@mui/material/CircularProgress';
import {
    Table,
    TableBody,
    TableCell,
    TableHead,
    TableRow,
} from "@mui/material";
import { ToastContainer, toast } from "react-toastify";
import 'react-toastify/dist/ReactToastify.css';
import GlobalModal from "../Modal";
import "./addOrgEvents.css"


const AddOrganisationEvents = () => {

    const [eventName, setEventName] = React.useState("");
    const [eventDate, setEventDate] = React.useState(null);
    const [eventDate2, setEventDate2] = React.useState(null);
    const [imageFiles, setImageFiles] = React.useState([]);
    const [audio, setAudio] = React.useState(null);
    const [addEventObj, setAddEventObj] = React.useState([]);
    const [eventList, setEventList] = useState([]);
    const [isLoading, setIsLoading] = React.useState(false);
    const [disabledBtn, setDisabledBtn] = React.useState(false);
    const [viewData, setViewData] = React.useState(null);
    const [modalOpen, setModalOpen] = useState(false);
    const [deleteData, setDeleteData] = useState(null);
    const orgId = localStorage.getItem('orgId');

    useEffect(() => {
        getOrgEventsList();
      }, []);

    const getOrgEventsList = () => {
        setIsLoading(true);
        axios({
          method: "get",
          url: `http://13.235.70.211:9090/v1/org/event/${orgId}`,
          config: { headers: { "Content-Type": "application/json" } },
        })
          .then((res) => {
            console.log(res,'ooooo');
            if (res) {
              if (res.data) {
                console.log("Event List :",eventList)
                setEventList(res.data);
                setTimeout(() => {
                  setIsLoading(false);
                }, 2000);
              }
            }
          })
          .catch((err) => {
            console.log(err.response,'222');
            if(err){
              if(err.response){
                if(err.response.data){
                  if(err.response.data.responseCode === 403){
                    toast.error("Please add your organisation events");
                    setEventList([]);
                    setTimeout(() => {
                      setIsLoading(false);
                    }, 1000);
                  }else{
                    toast.error(err.response.data);
                    setTimeout(() => {
                      setIsLoading(false);
                    }, 1000);
                  }
    
                }
              }
            }
        });
    };

    const handleView = (el,type) => {
        setViewData(el);
        // setViewType(type);
        // setModalOpen(true);
    };

    const handleChangeEventName = (event) => {
        setEventName(event.target.value);
    };

    const handleDay = (e) => {
        console.log(e);
        const ev = moment(e).format("YYYY-MM-DD");
        console.log(ev, eventName);
        setEventDate(ev)
        setEventDate2(e)
    };

    const handleImagesUpload = (e) => {
        const files = Array.from(e.target.files);
        toast.success('Image(s) are added!');
        setImageFiles({ event: files });
    };

    const handleAudioUpload = (e) => {
        const files = e.target.files[0];
        toast.success('Audio(s) are added!');
        setAudio({ event: files });
    };

    const handleAddEvent = () => {
        if (!eventName || !eventDate || imageFiles.length === 0 || !audio) {
            toast.error('All details are required! >>');
            return;
        }
        if (imageFiles && eventName && eventDate && imageFiles && audio) {
            const data = {
                orgId: orgId,
                eventName: eventName,
                eventDate: eventDate
            };

            let formData = new FormData();

            const json = JSON.stringify(data);
            const blob = new Blob([json], {
                type: 'application/json'
            });
            formData.append('data', blob);

            console.log("Images Files : ", imageFiles)

            if(imageFiles.event.length > 0){
                for(let i=0;i<imageFiles.event.length;i++){
                    formData.append(
                        "images",
                        imageFiles.event[i]
                    );
                }
            }

            formData.append('audio', audio.event);

            setIsLoading(true);
            setDisabledBtn(true);

            axios.post("http://13.235.70.211:9090/v1/org/event", formData, {
                headers: { "Accept": "application/json", "Content-Type": "multipart/form-data" },
            })
            .then((response) => {
                if (response.data) {
                    toast.success(`Organisation event has been created successfully`);
                    getOrgEventsList();
                    setEventName("");
                    setEventDate(null);
                    setEventDate2(null);
                    setImageFiles([]);
                    setAudio(null);
                    setIsLoading(false);
                    setDisabledBtn(false);
                }
            })
            .catch((err) => {
                setIsLoading(false);
                setDisabledBtn(false);
                if (err.response) {
                    if (err.response.status === 403) {
                        toast.error('Organisation event has already been created');
                    } else if (err.response.data && err.response.data.error) {
                        toast.error(err.response.data.error);
                    } else {
                        toast.error('An error occurred!');
                    }
                } else {
                    toast.error('An error occurred!');
                }
            });
        } else {
          if(imageFiles == null && eventName == '' && eventDate == null && audio == null ){
            toast.error('Event Name, Event Date, Audio and Images are required!');
          }else if(imageFiles && eventName == '' && eventDate == null && audio == null){
            toast.error('Event Name, Event Date and Audio are required!');
          }else if(imageFiles == null && eventName && eventDate == null && audio == null){
            toast.error('Event Date, Audio and Images are required!');
          }else if(imageFiles !== null && eventName && eventDate == null){
            toast.error('Event Date is required!');
          } else if(imageFiles !== null && eventName && eventDate !== null && audio == null){
            toast.error('Audio is required!');
          } else{
            toast.error('Images are required!');
          }
        }

    };

    const deleteEvent = (el) => {
        setDeleteData(el);
        setModalOpen(true);
    };

    const handleToastMessage = () => {
        getOrgEventsList();
        toast.success("Event has been deleted successfully");
    };

    const handleClose = () => {
        setModalOpen(false);
    };


    return (
        <div className="addmembers-page">
      
            <ToastContainer
                position="top-center"
                autoClose={3000}
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover
                toastStyle={{color:'#000000', fontWeight:'lighter', fontFamily:'Open Sans, sans-serif', fontSize:'12px'}}
            />

            <GlobalModal
                modalOpen={modalOpen}
                handleToastMessage={handleToastMessage}
                deleteData={deleteData}
                handleClose={handleClose}
                deleteType="event"
            />

            {
                isLoading ?
                <div className="addmember-loader">
                    <CircularProgress />
                </div>
                : ''
            }
  
            <div className="add-title"> Add Organisation Event Details</div>
            <div className="add-sub-title">
                Add details of new project on Special day wisher, and make their day
                special with us.
            </div>
            <div className="personal-card">
            <Grid container spacing={2} sx={{ marginTop: "1rem" }}>
                <Grid container item xs={12}>
                    <div className="member-detail-title">
                    Event details and Photo Upload
                    <div className="member-detail-desc">
                        Below images are used for creating video's and minimum 3 to
                        maximum 10 images are allowed to upload.
                    </div>
                    </div>
                </Grid>
                <Grid container item xs={12} sm={12} sx={{ marginTop: "1rem" }}>
                    <Grid container item xs={12} sm={5}>
                    <TextField
                        id="outlined-select-currency"
                        label="Event Name"
                        value={eventName}
                        size="small"
                        onChange={handleChangeEventName}
                        className="event-select"
                        // sx={{ marginTop: "0.5rem", width:'94%' }}
                        variant="outlined"
                        inputProps={{ style: { fontSize: 13 } }}
                        InputLabelProps={{ style: { fontSize: 12 } }}
                    />
                    <DatePicker
                        selected={eventDate2}
                        onChange={(e) => handleDay(e)}
                        className="date-picker"
                        placeholderText="DD-MM-YYYY"
                        InputLabelProps={{}}
                    />
                    <div className="mobile-image-upload">
                        <label htmlFor="mob-contained-button-file">
                        <Input
                            accept="image/*"
                            id="mob-contained-button-file"
                            multiple
                            fullWidth
                            type="file"
                            sx={{ display: "none", width:'90%' }}
                            onChange={(e) => handleImagesUpload(e)}
                            inputProps={{
                            multiple: true,
                            }}
                        />
                        <Button variant="outlined" 
                            style={{width:'100%'}} component="span">
                            Upload Images
                            <IconButton
                                color="primary"
                                aria-label="upload picture"
                                component="span"
                            >
                            <PhotoCamera />
                            </IconButton>
                            <div className="upload-text">
                            *.png, .jpg, .jpeg  are allowed
                            </div>
                        </Button>
                        </label>
                    </div>

                    <div className="mobile-image-upload">
                        <label htmlFor="contained-button-file">
                        <Input
                            accept=".mp3,audio/*"
                            id="contained-button-file"
                            multiple
                            fullWidth
                            type="file"
                            sx={{ display: "none", width:'90%' }}
                            onChange={(e) => handleAudioUpload(e)}
                            inputProps={{
                            multiple: true,
                            }}
                        />
                        <Button variant="outlined" 
                            style={{width:'100%'}} component="span">
                            Upload Audio
                            <IconButton
                                color="primary"
                                aria-label="upload picture"
                                component="span"
                            >
                            <PhotoCamera />
                            </IconButton>
                            <div className="upload-text">
                            *.mp3, .wav, .mp4  are allowed
                            </div>
                        </Button>
                        </label>
                    </div>

                    <div className="add-event-btn-div">
                        <button onClick={handleAddEvent} disabled={disabledBtn ? true : false} className="add-event-btn" >
                            Add Event
                        </button>
                    </div>
                    </Grid>

                    <Grid container item xs={12} sm={7}>
                    <div className="event-list">
                        <Table>
                        <TableHead>
                            <TableRow>
                            <TableCell width={100}>Event Name </TableCell>
                            <TableCell width={50}>Total Images </TableCell>
                            <TableCell width={100}>Audio </TableCell>
                            <TableCell width={120}>Date</TableCell>
                            <TableCell width={100}>Actions</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {eventList.length > 0 ?
                            eventList.map((el, i) => {
                                return (
                                <TableRow key={i}>
                                    <TableCell>{el.eventName}</TableCell>
                                    <TableCell>
                                    {
                                        el.imageUrls &&
                                        el.imageUrls.length}
                                    </TableCell>
                                    <TableCell>{el.audioUrl.split('/').pop()}</TableCell>
                                    <TableCell>{el.eventDate}</TableCell>
                                    <TableCell>
                                    <span>
                                    
                                        <RemoveRedEyeIcon
                                        onClick={()=>handleView(el, 'members')}
                                        style={{
                                            color: "#ED4D6E",
                                            cursor: "pointer",
                                        }}
                                        />{" "}
                                    </span>
                                    <span>
                                        <DeleteIcon
                                        onClick={()=>deleteEvent(el)} 
                                        style={{
                                            color: "#ED4D6E",
                                            cursor: "pointer",
                                            marginLeft:'15px',
                                        }}
                                        />{" "}
                                    </span>
                                
                                    </TableCell>
                                </TableRow>
                                );
                            })
                            : ''
                            }
                        </TableBody>
                        </Table>
                    </div>
                    </Grid>
                </Grid>
                <Grid container item xs={12} sm={12}></Grid>
                </Grid>
            </div>
        </div>
    )
};

export default AddOrganisationEvents;