import React, {useEffect} from "react";
import TextField from "@mui/material/TextField";
import { Grid } from "@mui/material";
import MenuItem from "@mui/material/MenuItem";
import Button from "@mui/material/Button";
import Input from "@mui/material/Input";
import IconButton from "@mui/material/IconButton";
import PhotoCamera from "@mui/icons-material/PhotoCamera";
import { ToastContainer, toast } from 'react-toastify';
import axios from 'axios';
import 'react-toastify/dist/ReactToastify.css';
import "./audioList.css";
import CircularProgress from '@mui/material/CircularProgress';

let eventNameArray = [
  {
    value: "BIRTH_DATE",
    label: "Birthday",
    files: [],
  },
  {
    value: "WEDDING_DATE",
    label: "Wedding Day",
    files: [],
  },
  {
    value: "JOINING_DATE",
    label: "Joining Day",
    files: [],
  },
  {
    value: "COMMON",
    label: "ALL",
    files: [],
  },
];
const AudioList = () => {
  const [eventName, setEventName] = React.useState("BIRTH_DATE");
  const [audioList, setAudioList] = React.useState([]);
  const [audioDesc, setAudioDesc] = React.useState("");
  const [audioFiles, setAudioFiles] = React.useState(null);
  const [all, setAll] = React.useState(null)
  const [birthday, setBirthday] = React.useState(null);
  const [weddingDay, setWeddingDay] = React.useState(null);
  const [joiningDay, setJoiningDay] = React.useState(null);
  const [birthday2, setBirthday2] = React.useState(null);
  const [weddingDay2, setWeddingDay2] = React.useState(null);
  const [joiningDay2, setJoiningDay2] = React.useState(null);
  const [isLoading, setIsLoading] = React.useState(false);
  const [addDisable, setAddDisable] = React.useState(false);
  const [type, setType] = React.useState('');
  const orgId = localStorage.getItem('orgId');


  const handleChangeEventName = (event) => {
    setEventName(event.target.value);
  };
  const handleSearchType = (event) => {
    setType(event.target.value);
  };


  useEffect(()=>{
    getAudioList();
  },[])

  const handleSearchAudio = () => {
    setIsLoading(true);
    console.log(type, type.toUpperCase())
    axios({
      method: "get",
      url: `http://13.235.70.211:9090/v1/audio/${orgId}/${type.toUpperCase()}`,
      config: { headers: { "Content-Type": "application/json" } },
    })
      .then((res) => {
        console.log(res);
        setType('')
        if (res) {
          if (res.data) {
            setAudioList(res.data);

            setTimeout(() => {
              setIsLoading(false);
            }, 2000);
          }
        }
      })
      .catch((err) => {
        console.log(err.response);
        setType('')
        setIsLoading(false);
      });
  }

  const getAudioList = () => {
    setIsLoading(true);
    axios({
      method: "get",
      url: `http://13.235.70.211:9090/v1/audio/${orgId}`,
      config: { headers: { "Content-Type": "application/json" } },
    })
      .then((res) => {
        console.log(res);
        if (res) {
          if (res.data) {
            setAudioList(res.data);

            setTimeout(() => {
              setIsLoading(false);
            }, 2000);
          }
        }
      })
      .catch((err) => {
        console.log(err.response);
        setIsLoading(false);
      });
  };




  const handleAudiosUpload = (e) => {
        console.log(e.target.files, eventName, "imageupload");
   
      if(eventName === 'BIRTH_DATE' ){
        eventNameArray.forEach((el, i) => {
          if (eventName === el.value) {
            el.files = e.target.files;
          }
        });
        toast.success('Audio(s) are added!');
        setAudioFiles({ event: e.target.files });
      }else  if(eventName === 'WEDDING_DATE' ){
        eventNameArray.forEach((el, i) => {
          if (eventName === el.value) {
            el.files = e.target.files;
          }
        });
        toast.success('Audio(s) are added!');
        setAudioFiles({ event: e.target.files });
      } else  if(eventName === 'JOINING_DATE'  ){
        eventNameArray.forEach((el, i) => {
          if (eventName === el.value) {
            el.files = e.target.files;
          }
        });
        toast.success('Audio(s) are added!');
        setAudioFiles({ event: e.target.files });
      } else  if(eventName === 'COMMON'){
        eventNameArray.forEach((el, i) => {
          if (eventName === el.value) {
            el.files = e.target.files;
          }
        });
        toast.success('Audio(s) are added!');
        setAudioFiles({ event: e.target.files });
      }
      
      else if(!eventName){
         toast.error('Event Name is required!');
      }
  
  }

  const handleSubmitAudios = () => {
    setAddDisable(true);
    let file;
    eventNameArray.forEach((el)=>{
      if(el.value === eventName){
         file = el.files.length > 0 && el.files;
      }
    })

    let formData = new FormData();
    const data = {
      audioDescription: audioDesc,
      audioType: eventName,
      orgId: orgId,
      preferred: true
    }

    const json = JSON.stringify(data);
    const blob = new Blob([json], {
      type: 'application/json'
    });
    formData.append('data', blob);
    for(let i=0;i<file.length;i++){
      formData.append(
        "file",
        file[i]
      );
    }


    axios({
      method: "post",
      url: `http://13.235.70.211:9090/v1/audio`,
      data: formData,
      headers: {  "Accept": "application/json" ,"Content-Type": "multipart/form-data" },
    })
    .then((res)=>{
      console.log(res,'res');
      setAddDisable(false);
      setAudioDesc("");
      setEventName("BIRTH_DATE");
      getAudioList();
        if(res){
          if(res.data){
            if(res.data.responseCode == 200){
              toast.success('Data has been updated successfully!');

            }
          }
        }
    })
    .catch((err)=>{
      console.log(err);
      setAddDisable(false);
      setAudioDesc("");
      setEventName(null)
    })
  }

  return (
    <div className="audio-page">
               <ToastContainer
            position="top-center"
            autoClose={3000}
            hideProgressBar={false}
            newestOnTop={false}
            closeOnClick
            rtl={false}
            pauseOnFocusLoss
            draggable
            pauseOnHover
            toastStyle={{color:'#000000', fontWeight:'lighter', fontFamily:'Open Sans, sans-serif', fontSize:'12px'}}
        />
      <div className="audio-title">Audio Management</div>
      <div className="audio-sub-title">Manage audio for events.</div>

      <Grid container spacing={2}  >
        <Grid container item xs={12} sm={12} md={5}>
          <div className="audio-card-head">
            <Grid container item xs={12}>
              <div>Upload Event Based Audio</div>
              <div className="baseline"></div>
            </Grid>

            <TextField
              id="outlined-select-currency"
              select
              fullWidth
              label="Event Name"
              value={eventName}
              size="small"
              onChange={handleChangeEventName}
              sx={{ marginTop: "2rem" }}
              variant="outlined"
              inputProps={{ style: { fontSize: 13 } }}
              InputLabelProps={{ style: { fontSize: 12 } }}
            >
              {eventNameArray.map((option) => (
                <MenuItem key={option.value} value={option.value}>
                  {option.label}
                </MenuItem>
              ))}
            </TextField>

            <TextField
              fullWidth
              id="outlined-basic"
              size="small"
              label="Audio Description"
              variant="outlined"
              inputProps={{ style: { fontSize: 13 } }}
              InputLabelProps={{ style: { fontSize: 12 } }}
              sx={{ marginTop: "1rem" }}
              value={audioDesc}
              onChange={(e)=>setAudioDesc(e.target.value)}
            />

            <div className="support-text">
              * Supported files .mp3, .wav, .m4a, .mp4{" "}
            </div>

            <Grid container item xs={12} sm={2} sx={{ marginTop: "1rem" }}>
              <label htmlFor="contained-button-file">
                <Input
                  
                  id="contained-button-file"
                  multiple
                  type="file"
                  sx={{ display: "none" }}
                  onChange={(e) => handleAudiosUpload(e)}
                  accept=".mp3,audio/*"
                />
                <Button
                  variant="outlined"
                  style={{
                    width: "300px",
                    height: "45px",
                    color: "#ED4D6E",
                    border: "1px solid #ED4D6E",
                  }}
                  component="span"
                >
                  Upload
                  <IconButton
                    style={{ color: "#ED4D6E" }}
                    aria-label="upload picture"
                    component="span"
                  >
                    <PhotoCamera />
                  </IconButton>
                </Button>
              </label>
            </Grid>

            <button disabled={addDisable} onClick={handleSubmitAudios} className={addDisable ? "audio-add-disable-btn" : "audio-add-btn"}>Audio Add Details</button>
          </div>
        </Grid>
        <Grid container item xs={12} sm={12}  md={7}>
        
          <div className="audio-cardlist-head">
            <Grid container item xs={12}>
          
              <div className="audio-list-title">Audio List</div>
              
              <div className="audio-search-div">
              <TextField
              id="outlined-select-currency"
              select
              label="Search By Type"
              value={type}
              size="small"
              onChange={handleSearchType}
              style={{width:'300px', height:"30px"}}
              variant="outlined"
              inputProps={{ style: { fontSize: '14px', fontFamily:'Open Sans, sans-serif' } }}
              InputLabelProps={{ style: { fontSize: '12px' } }}
            >
              {eventNameArray.map((option) => (
                <MenuItem key={option.value} value={option.value}>
                  {option.label}
                </MenuItem>
              ))}
            </TextField>
                
                <button
                  onClick={() => handleSearchAudio()}
                  className={"audio-search-btn"}
                >
                  Search
                </button>
                </div>
              
         
              <div className="baseline"></div>
            </Grid>
            <Grid container item xs={12}>
              {
                isLoading ? 
                <div className="audiolist-loader">
                  <CircularProgress />
                </div>
                :
                <div className="audio-table-div">
                <table>
                <thead>
                  <tr>
                    <td>Audio</td>
                    <td>Type</td>
                    <td>Audio Url</td>
                    
                  </tr>
                </thead>

                <tbody>
                  {audioList.length > 0 ? (
                    audioList.map((el, i) => {
                      return (
                        <tr key={el.audioId}>
                          <td>{el.audioDescription}</td>
                          <td>{el.audioType}</td>
                          <td>{el.audioUrl.split('/').pop()}</td>
                         
                          {/* <td>
                            <div>
                              <span>
                                <DeleteForeverIcon
                                  onClick={() => handleDelete(el)}
                                  style={{
                                    color: "#ED4D6E",
                                    cursor: "pointer",
                                  }}
                                />{" "}
                              </span>
                              <span>
                                <EditIcon
                                  style={{
                                    color: "#ED4D6E",
                                    cursor: "pointer",
                                  }}
                                />{" "}
                              </span>
                            </div>
                          </td> */}
                        </tr>
                      );
                    })
                  ) : (
                    <div className="audiolist-no-data"> No Data Available </div>
                  )}
                </tbody>
              </table>
              </div>
              }
           
            </Grid>
            {/* <div className="audio-sub-title">No Audio Files</div> */}
          </div>
        </Grid>
      </Grid>
    </div>
  );
};

export default AudioList;
