import React, { useState } from "react";
import Logo from "./../../assets/wisher-logo-sm.png";
import { TextField } from "@mui/material";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import "./login.css";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import GlobalModal from "../Modal";

const Login = () => {
  const navigate = useNavigate();

  const [loginData, setLoginData] = useState({emailId:"", password:""})
  const [isLoading, setIsLoading] = useState(false);
  const [disabledBtn, setDisabledBtn] = useState(false);

  const handleChange = (e) => {
    setLoginData(prev => ({...prev, [e.target.name] : e.target.value}))
  }

  const handleSubmitLogin = (e) => {
    localStorage.removeItem('token');
    localStorage.removeItem('orgId');
    e.preventDefault();
    if(loginData.emailId && loginData.password) {
      axios.post('http://13.235.70.211:9090/login', loginData, {
        headers: {
          'Content-Type': 'application/json'
        },
        // withCredentials: true, 
      })
      .then((response) => {
        if(response){
          if(response.data.responseCode == 201){
            setLoginData({})
            console.log(response.data)
            localStorage.setItem('token', response.data.description.token);
            localStorage.setItem('orgId', response.data.description.organisation.orgId);
            localStorage.setItem('organisationName', response.data.description.organisation.organisationName);
            localStorage.setItem('emailId', response.data.description.organisation.emailId);
            toast.success(`Organisation admin logged in successfully >> ${response?.data?.description}`);
            navigate("/home/dashboard")
            setIsLoading(false);
            setDisabledBtn(false);
          }
        }
      })
      .catch((err) => {
        if (err) {
          if (err) {
            if (err.data) {
              toast.error(err.data.description);
            } else {
              toast.error('An error occurred, please try again.');
            }
          } else {
            toast.error('No response from the server.');
          }
        } else {
          toast.error('An error occurred, please try again.');
        }
        console.error('There was an error!', err);
      })
      .finally(() => {
        setIsLoading(false);
        setDisabledBtn(false);
      });
    } else{
      setTimeout(()=>{
        setIsLoading(false);
        setDisabledBtn(false);
      },1000)
      toast.error('All details are required!')
    }
  }

  return (
    <div className="login-body">
      <ToastContainer
            position="top-center"
            autoClose={3000}
            hideProgressBar={false}
            newestOnTop={false}
            closeOnClick
            rtl={false}
            pauseOnFocusLoss
            draggable
            pauseOnHover
            toastStyle={{color:'#000000', fontWeight:'lighter', fontFamily:'Open Sans, sans-serif', fontSize:'12px'}}
        />
       {/* <GlobalModal modalOpen={modalOpen}  viewData={viewData} viewType={viewType}  handleviewClose={handleClose} /> */}
      <div className="login-form-card">
        <div className="form-title">
          <img src={Logo} height="50" alt="prop" />{" "}
        </div>

        <div className="form-div">
          <div className="input-field">
            <TextField fullWidth id="outlined-required" label="Email" type="email" name="emailId" value={loginData.emailId} onChange={(e) => handleChange(e)} />
          </div>
          <div className="input-field">
            <TextField
              fullWidth
              name="password"
              id="outlined-password-input"
              label="Password"
              type="password"
              value={loginData.password}
              onChange={(e) => handleChange(e)}
            />
          </div>

          <div className="login-btn-div">
            <button
              onClick={(e) => handleSubmitLogin(e)}
              className="login-btn"
            >
              Login
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};
export default Login;
